import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { RouterModule } from '@angular/router';
import { MaterialModule } from '@jgpl/shared';
import { WebfooterModule } from '@jgpl/shared/webfooter/webfooter.module';
import { WebheaderModule } from '@jgpl/shared/webheader/webheader.module';
import { TermComponent } from './term.component';



@NgModule({
  declarations: [],
  imports: [
    CommonModule,
    MaterialModule,
    WebheaderModule,
    WebfooterModule,
    RouterModule.forChild([
      { path: '', component: TermComponent  }
    ])
  ]
})
export class TermModule { }
